import { Component, Input, OnInit } from '@angular/core';
import { ObservableModalAbstractComponent } from '@polpware/bs-components';
import { AlertDefaultImpl, IHasAlertFeature } from '@polpware/ngx-alert';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';


export interface IMultiDateModalInput {
    title: string;
    initValue: string[];
}

@Component({
    selector: 'polp-bs-multi-date-modal',
    templateUrl: './multi-date-modal.component.html',
    styleUrls: ['./multi-date-modal.component.css']
})
export class MultiDateModalComponent extends ObservableModalAbstractComponent<IMultiDateModalInput, string[]>
    implements OnInit, IHasAlertFeature {

    @Input() title: string = '';
    @Input() initValue: string[] = [];

    outputValue: string[];
    isValid: boolean = false;

    alertProvider = new AlertDefaultImpl();

    constructor(
        public readonly bsModalRef: BsModalRef,
        protected readonly bsModalService: BsModalService) {
        super();
    }

    get alerts() {
        return this.alertProvider.data;
    }

    ngOnInit(): void {
    }

    close() {
        this.closeModal(null);
    }

    updateValue(evt: Array<{ display: string; value: string }>) {
        if (evt) {
            this.outputValue = evt.map(a => a.value);
        }
    }

    validate(evt: { valid: boolean }) {
        if (evt) {
            this.isValid = evt.valid;
        }
    }

    confirm() {
        if (this.isValid) {
            this.closeModal(this.outputValue);
        }
    }


}
