import { Injectable } from '@angular/core';
import { BsModalService } from 'ngx-bootstrap/modal';
import { IMultiDateModalInput, MultiDateModalComponent } from './multi-date-modal/multi-date-modal.component';
import { IScheduleTimeModalInput, ScheduleTimeModalComponent } from './schedule-time-modal/schedule-time-modal.component';

@Injectable({
    providedIn: 'root'
})
export class UtilsService {

    constructor(private readonly _modalService: BsModalService) { }

    showScheduleTimeEditorAsync(input: IScheduleTimeModalInput) {
        const modalRef = this._modalService.show(ScheduleTimeModalComponent, {
            animated: true,
            ignoreBackdropClick: true,
            initialState: Object.assign({}, input),
            keyboard: false,
            class: 'modal-dialog-centered'
        });

        return modalRef.content.result.toPromise();
    }

    showMultiDateEditorAsync(input: IMultiDateModalInput) {
        const modalRef = this._modalService.show(MultiDateModalComponent, {
            animated: true,
            ignoreBackdropClick: true,
            initialState: Object.assign({}, input),
            keyboard: false,
            class: 'modal-dialog-centered'
        });

        return modalRef.content.result.toPromise();
    }

}
