import { Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, ValidationErrors, ValidatorFn } from '@angular/forms';
import { safeParseInt } from '@polpware/fe-utilities';
import { DefaultFormBaseComponent } from '@polpware/ngx-form-common';
import { Subscription } from 'rxjs';

function getFormattedDate(date) {
    var month = (1 + date.getMonth()).toString();
    month = month.length > 1 ? month : '0' + month;

    var day = date.getDate().toString();
    day = day.length > 1 ? day : '0' + day;

    return month + '/' + day;
}

const formValidator: ValidatorFn = (control: FormGroup): ValidationErrors | null => {

    let v: { display: string; value: string } | Array<{ display: string; value: string }> = (control.get('chips') as FormControl).value;
    if (v) {
        if (!Array.isArray(v)) {
            v = [v];
        }
        const someWrong = v.some(a => {
            const b = a.value;
            if (!b) {
                return true;
            }
            const c = b.split('/');
            if (c.length != 2) {
                return true;
            }
            const m = safeParseInt(c[0]);
            if (m < 1 || m > 12) {
                return true;
            }
            const d = safeParseInt(c[1]);
            if (d < 0 || d > 31) {
                return true;
            }
        });

        if (someWrong) {
            return { chips: true };
        }
    }

    return null;
};


@Component({
    selector: 'polp-bs-multi-date-picker',
    templateUrl: './multi-date-picker.component.html',
    styleUrls: ['./multi-date-picker.component.css']
})
export class MultiDatePickerComponent extends DefaultFormBaseComponent
    implements OnInit {

    @Input() initValue: string[] = [];

    prefix = 'mdp-' + (new Date).getTime() + '-';

    bsValue = new Date();

    items: Array<{
        display: any;
        value: any;
    }> = [];

    form: FormGroup;

    private _subr: Subscription;

    constructor(private _builder: FormBuilder) {
        super();

        this.form = this._builder.group({
            'chips': []
        }, { validators: [formValidator] });

    }

    ngOnInit(): void {

        if (this.initValue) {
            const items = this.initValue.map(a => {
                return {
                    display: a,
                    value: a
                };
            });
            setTimeout(() => {
                this.form.setValue({
                    chips: items
                });
            });
        }


        this._subr = this.form.valueChanges.subscribe(a => {
            this.notifyValidation();
            this.notifyValueChanges(a.chips);
        });

    }

    ngOnDestroy() {
        this._subr.unsubscribe();
    }


    confirm() {
        if (this.bsValue) {
            const items = this.form.value.chips || [];
            const v = getFormattedDate(this.bsValue);
            const newItems = [...items, {
                display: v,
                value: v
            }];

            this.form.setValue({
                chips: newItems
            });
        }
    }

}
