import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { faSpinner } from '@fortawesome/free-solid-svg-icons';
import { ObservableModalAbstractComponent, IHasChildModal, IChildModalState } from '@polpware/bs-components';
import { AlertDefaultImpl, IHasAlertFeature } from '@polpware/ngx-alert';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { IScheduleTime } from '../interfaces';
import { ISettings } from '../schedule-time-picker/schedule-time-picker.component';

export interface IScheduleTimeModalInput {
    title: string;
    initSettings: ISettings;
    initValue: IScheduleTime;
    extraClasses: string;
    onConfirmAsync: (data: IScheduleTime) => Promise<any>;
}

@Component({
    selector: 'polp-bs-schedule-time-modal',
    templateUrl: './schedule-time-modal.component.html',
    styleUrls: ['./schedule-time-modal.component.css']
})
export class ScheduleTimeModalComponent
    extends ObservableModalAbstractComponent<IScheduleTimeModalInput, IScheduleTime>
    implements OnInit, IHasAlertFeature, IHasChildModal {

    faSpinner = faSpinner;

    @Input() title: string = '';
    @Input() initSettings: ISettings;
    @Input() initValue: IScheduleTime;
    @Input() onConfirmAsync: (data: IScheduleTime) => Promise<any>;
    // We need this one to get the init class.
    @Input() extraClasses: string;

    outputValue: IScheduleTime;
    isValid: boolean;

    isSaving: boolean;
    alertProvider = new AlertDefaultImpl();

    showBackdrop: boolean;

    constructor(
        public readonly bsModalRef: BsModalRef,
        protected readonly bsModalService: BsModalService) {
        super();
    }

    get alerts() {
        return this.alertProvider.data;
    }

    ngOnInit(): void {
    }

    close() {
        this.closeModal(null);
    }

    updateScheduler(evt: IScheduleTime) {
        this.outputValue = evt;
    }

    validateScheduler(evt: { valid: boolean }) {
        if (evt) {
            this.isValid = evt.valid;
        }
    }

    updateStyle(evt: IChildModalState) {
        if (evt && evt.opened) {
            const newClasses = this.extraClasses ? `${this.extraClasses} has-child-modal` : 'has-child-modal';
            this.bsModalRef.setClass(newClasses);
        } else {
            const newClasses = this.extraClasses || '';
            this.bsModalRef.setClass(newClasses);
        }
    }

    async confirmAsync() {
        this.alertProvider.clean();
        if (!this.isValid) {
            this.alertProvider.warning('polpCronJob.general');
            return;
        }

        this.isSaving = true;
        this.alertProvider.info('polpCronJob.messages.working');
        try {
            if (this.onConfirmAsync) {
                await this.onConfirmAsync(this.outputValue);
            }
            this.closeModal(this.outputValue);
        } catch (ex) {
            this.alertProvider.clean();
            this.alertProvider.danger('polpCronJob.errors.somethingWrong');
        } finally {
            this.isSaving = false;
        }
    }

}
